                <router-link to="/admin/products" class="nav-link"
                :class="{'active': currentPage === '/admin/products'}"
                >
                  <span class="d-block w-100" data-feather="home" @click="currentPage = '/admin/products'">產品列表</span>
                </router-link>
              </li>
              <li class="nav-item">
                <router-link to="/admin/orders" class="nav-link"
                  :class="{'active': currentPage === '/admin/orders'}"
                  >
                  <span class="d-block w-100" data-feather="file" @click="currentPage = '/admin/orders'">訂單列表</span>
                </router-link>
              </li>
              <li class="nav-item">
                <router-link to="/admin/coupons" class="nav-link"
                :class="{'active': currentPage === '/admin/coupons'}"
                >
                  <span class="d-block w-100" data-feather="file" @click="currentPage = '/admin/coupons'">優惠券</span>
                </router-link>
              </li>
            </ul>
            <h6
              class="sidebar-heading d-flex justify-content-between align-items-center px-3 mt-4 mb-1 text-muted"
            >
              <span>模擬功能</span>
              <a
