                href="#"
                aria-label="Add a new report"
              >
                <span data-feather="plus-circle"></span>
              </a>
            </h6>
            <ul class="nav flex-column mb-2">
              <li class="nav-item">
                <router-link to="/customer_orders" class="nav-link"
                :class="{'active': currentPage === '/customer_orders'}">
                  <span class="d-block w-100" data-feather="file-text" @click="currentPage = '/customer_orders'">模擬訂單</span>
                </router-link>
              </li>
            </ul>
          </div>
        </nav>
  </div>
</template>

<script>
export default {
  name: 'sidebar',
  props: {
    msg: String
  },
  data () {
    return {
      currentPage: ''
    }
  },
  methods: {
    Page () {
      this.currentPage = this.$route.path
      // console.log('現在網址', this.currentPage)
    }
  },
  watch: {
    url () {
      console.log(this.$route.path)
    }
  },
  created () {
    this.Page()
  }
}
// 當別的元件產生的時候，取得他們自己的網址，然後將網址的資料內容回傳給sidebar的currentPage，可以做到嗎?
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/*
 * Sidebar
 */

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100; /* Behind the navbar */
  padding: 48px 0 0; /* Height of navbar */
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}

@media (max-width: 767.98px) {
  .sidebar {
    top: 5rem;
  }
}

.sidebar-sticky {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
}

@supports ((position: -webkit-sticky) or (position: sticky)) {
  .sidebar-sticky {
    position: -webkit-sticky;
    position: sticky;
  }
}

.sidebar .nav-link {
  font-weight: 500;
  color: #333;
}

.sidebar .nav-link .feather {
  margin-right: 4px;
  color: #999;
}

.sidebar .nav-link.active {
  color: #007bff;
}

.sidebar .nav-link:hover .feather,
.sidebar .nav-link.active .feather {
  color: inherit;
}

.sidebar-heading {
  font-size: .75rem;
  text-transform: uppercase;
}

</style>



