<template>
  <div class="login py-3">
    <!-- 登入介面 -->
    <form class="form-signin bg-custom" @submit.prevent="Signin">
      <h1 class="h3 my-4 font-weight-normal text-center">管理員登入</h1>
      <label for="inputEmail" class="sr-only">Email address</label>
      <input type="email" id="inputEmail" v-model="user.username" class="form-control"
        placeholder="Email address" required autofocus>
      <label for="inputPassword" class="sr-only">Password</label>
      <input type="password" id="inputPassword" class="form-control"
        placeholder="Password" required
        v-model="user.password">
      <div class="checkbox mb-3">
        <label>
          <input type="checkbox" value="remember-me"> Remember me
        </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit" @click="Signin">管理員登入</button>
      <h5 class="text-center mt-4">不是管理員嗎?</h5>
      <button class="btn btn-lg btn-primary btn-block"><router-link to='customer_orders' class="text-white">用戶登入</router-link></button>
      <p class="mt-5 mb-3 text-muted text-center">&copy; 2017-2020</p>
    </form>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      user: {
        username: '',
        password: ''
      },
      test: '',
      newttest: ''
    }
  },
  methods: {
    // 登入
    Signin () {
      const api = `${process.env.APIPATH}/admin/signin`
      const vm = this
      this.$http.post(api, vm.user).then((response) => {
        // console.log(response.data)
        // 底下是帳號密碼
        // oblivion8895@gmail.com
        // c5h0o4r2d2
        if (response.data.success === true) {
          // console.log('登入成功')
          vm.$router.push('admin')
        } else {
          console.log('登入失敗!')
        }
      })
    }
  }
  // watch: {
  //   // 當test的值發生改變的時候，會執行底下的函式內容
  //   test: function () {
  //     this.newtest = this.test + '-321654'
  //   }
  // }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* 範例的CSS */
html,
body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.bg-custom{
  background-color: rgb(216, 216, 216);
}
</style>



