<template>
  <div class="customercheckout">
    <h1 class="text-center">結帳確認</h1>
    <div class="my-5 row justify-content-center">
      <form class="col-md-6" @submit.prevent="payOrder">
        <h3 class="text-center">購物車內容</h3>
        <table class="table">
          <thead>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-right">總計</td>
              <td class="text-right">{{ order.total }}</td>
            </tr>
          </tfoot>
        </table>
        <h3 class="text-center">購買人資訊</h3>
        <table class="table">
          <tbody>
            <tr>
              <th width="120">Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th>姓名</th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th>收件人電話</th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th>收件人地址</th>
              <td>{{ order.user.address }}</td>
            </tr>
            <tr>
              <th>付款狀態</th>
              <td>
                <span v-if="!order.is_paid">尚未付款</span>
                <span v-else class="text-success">付款完成</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-right" v-if="order.is_paid === false">
          <button class="btn btn-danger">確認付款去</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'customercheckout',
  data () {
    return {
      orderId: '',
      order: {
        user: {}
      }
    }
  },
  methods: {
    getOrder () {
      const vm = this
      // 取得單一訂單列表
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/order/${vm.orderId}`
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        console.log('取得單一訂單列表', response.data)
        vm.isLoading = false
        vm.order = response.data.order
      })
    },
    payOrder () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/pay/${vm.orderId}`
      vm.isLoading = true
      this.$http.post(api).then((response) => {
        console.log(response.data)
        if (response.data.success) {
          vm.getOrder()
        }
        vm.isLoading = false
      })
    }
  },
  created () {
    this.orderId = this.$route.params.orderId // 後面這個orderId是對應在index裡面path的動態orderId，也就是 path: 'customer_checkout/:orderId'
    console.log('該訂單的ID', this.orderId)
    this.getOrder()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



