<template>
  <div class="orders">
    <loading :active.sync="isLoading"></loading>
    <h1 class="text-center">管理員訂單列表</h1>
    <table class="table table-striped mt-4">
      <tr>
        <th width="120">購買日期</th>
        <th width="240">Email</th>
        <th>購買款項</th>
        <th width="100">應付金額</th>
        <th width="100">是否付款</th>
      </tr>
      <tr v-for="(item, key) in orders" :key="key">
        <td>{{item.convertDate}}</td>
        <td>{{item.user.email}}</td>
        <td>
          <p v-for="(product, key) in item.products" :key="key">{{product.product.title}} 數量：{{product.product.num}}{{product.product.unit}} </p>
        </td>
        <td class="text-right">{{item.total | currency}}</td>
        <td v-if="item.is_paid" class="text-center text-success">已付款</td>
        <td v-else class="text-center text-danger">尚未付款</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: 'orders',
  data () {
    return {
      isLoading: false,
      orders: [],
      unixtime: ''
    }
  },
  methods: {
    getOrders () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/orders`
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        console.log(response.data)
        vm.isLoading = false
        vm.orders = response.data.orders
        vm.unixTime()
      })
    },
    // 將unix timestamp轉換為日常用時間形式
    unixTime () {
      const vm = this
      vm.orders.forEach((item) => {
        const a = new Date(item.create_at * 1000)
        const year = a.getFullYear()
        const month = a.getMonth() + 1
        const date = a.getDate()
        const time = `${year}/${month}/${date}`
        // console.log(item.create_at, time)
        item.convertDate = time
      })
      // const a = new Date(vm.unixtime * 1000)
      // const year = a.getFullYear()
      // const month = a.getMonth() + 1
      // const date = a.getDate()
      // const time = `${year}/${month}/${date}`
      // console.log(time)
    }
  },
  created () {
    this.getOrders()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



