<template>
  <div class="customerorders">
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
        <div class="col-md-4 mb-4" v-for="(item, key) in products" :key="key">
        <div class="card border-0 shadow-sm h-100">
          <div style="height: 150px; background-size: cover; background-position: center"
            :style="{backgroundImage: `url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{item.title}}</a>
            </h5>
            <p class="card-text">{{item.content}}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <del class="h6">原價 {{item.origin_price}} 元</del>
              <div class="h5">現在只要 {{item.price}} 元</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm"
            @click="getProduct(item.id)">
              <i class="fas fa-spinner fa-spin"></i>
              查看更多
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
              @click="addtoCart(item.id)">
              <i class="fas fa-spinner fa-spin"></i>
              加到購物車
            </button>
            <!-- <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
              @click="removeCartItem(item.id)">
              <i class="fas fa-spinner fa-spin"></i>
              移除該筆訂單資料
            </button> -->
          </div>
        </div>
      </div>
    </div>
    <!-- 產品分頁的切換 -->
    <nav aria-label="Page navigation example">
      <ul class="pagination justify-content-center">
        <li class="page-item" :class="{'disabled': pagination.has_pre === false}">
          <a class="page-link" href="#" @click.prevent="getProducts(pagination.current_page - 1)">Previous</a>
        </li>
        <li class="page-item" v-for="page in pagination.total_pages" :key="page"
          :class="{'active': pagination.current_page === page}">
          <a class="page-link" href="#" @click.prevent="getProducts(page)">{{page}}</a>
        </li>
        <li class="page-item" :class="{'disabled': pagination.has_next === false}">
          <a class="page-link" href="#" @click.prevent="getProducts(pagination.current_page + 1)">Next</a>
        </li>
      </ul>
    </nav>
    <!-- 單一商品的Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{product.title}}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt=""/>
            <div class="form-group">
              <label for="title">{{product.content}}</label>
            </div>
            <div class="form-group">
              <label for="description">{{product.description}}</label>
            </div>
            <div class="d-flex justify-content-between">
              <div class="h5" v-if="!product.price">{{product.origin_price}}</div>
              <del class="h6" v-if="product.price">原價 {{product.origin_price}} 元</del>
              <div class="h5" v-if="product.price">現在只要 {{product.price}} 元</div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num" id="">
              <option :value="num" v-for="num in 10" :key="num">
                選購 {{num}} {{product.unit}}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <span class="text-muted text-rigth">小計{{product.price*product.num}}</span>
            <button type="button" class="btn btn-primary" @click="addtoCart(product.id, product.num)">加入購物車</button>
          </div>
        </div>
      </div>
    </div>
    <section class="py-4"></section>
    <!-- 購物車的列表內容 -->
    <h3 class="text-center">購物車清單</h3>
    <table class="w-75 mx-auto table ">
      <tr>
        <th width="120" class="text-center">移除</th>
        <th width="120" class="text-center">品名</th>
        <th width="120" class="text-center">數量</th>
        <th width="80" class="text-center">單價</th>
      </tr>
      <tbody>
        <tr v-for="(item, key) in carts" :key="key">
        <td class="text-center">
          <button class="btn btn-sm btn-outline-danger" @click="removeCartItem(item.id)">刪除</button>
        </td>
        <td class="">{{item.product.title}}
          <div v-if="item.coupon" class="text-success">已套用優惠券</div>
        </td>
        <td class="text-center">{{item.qty}}/{{item.product.unit}}</td>
        <td class="text-right">{{item.product.price}}</td>
      </tr>
      </tbody>
      <tfoot>
      <tr>
        <td colspan="3" class="text-right">總計</td>
        <td class="text-right">{{carttotal}}</td>
      </tr>
      <tr>
        <template v-if="cartfinaltotal < carttotal">
          <td colspan="3" class="text-right">折扣價</td>
          <td class="text-right">{{cartfinaltotal}}</td>
        </template>
      </tr>
      <tr>
        <td colspan="4">
          <div class="input-group">
            <input type="text" class="form-control" v-model="couponcode.code" placeholder="請輸入優惠碼">
            <div class="input-group-append">
              <button class="btn btn-sm btn-outline-primary" @click="appliedcoupon">套用優惠碼</button>
            </div>
          </div>
        </td>
      </tr>
      </tfoot>
    </table>
    <!-- 表單 -->
    <div class="w-75 mx-auto">
      <form class="" @submit.prevent="createOrder">
      <div class="my-2">
        <label for="username">收件人姓名</label>
        <input class="form-control" v-model="form.user.name" name="name" type="text"
          placeholder="請輸入姓名" id="username" v-validate="'required'" required
          :class="{'is-invalid': errors.has('name')}">
        <span class="text-danger d-block" v-if="errors.has('name')">姓名必須輸入</span>
      </div>
      <div class="my-2">
        <label for="useremail">Email</label>
        <input  class="form-control" v-model="form.user.email" name="email" type="text"
          placeholder="請輸入信箱" id="useremail" v-validate="'required|email'" required
          :class="{'is-invalid': errors.has('email')}">
        <span class="text-danger d-block" v-if="errors.has('email')">{{errors.first('email')}}</span>
      </div>
      <div class="my-2">
        <label for="usertel">連絡電話</label>
        <input class="form-control" v-model="form.user.tel" name="tel" type="text"
          placeholder="請輸入連絡電話" id="usertel" v-validate="'required'" required
          :class="{'is-invalid': errors.has('tel')}"
          maxlength="10">
        <span class="text-danger d-block" v-if="errors.has('tel')">聯絡電話必須輸入</span>
      </div>
      <div class="my-2">
        <label for="useraddress">收件人地址</label>
        <input class="form-control" v-model="form.user.address" name="address" type="text"
          placeholder="請輸入收件人地址" id="useraddress" v-validate="'required'" required
          :class="{'is-invalid': errors.has('address')}">
        <span class="text-danger d-block" v-if="errors.has('address')">收件人地址必須輸入</span>
      </div>
      <button class="btn btn-primary my-2">提交訂單</button>
    </form>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'customerorders',
  data () {
    return {
      product: {},
      products: [],
      pagination: {},
      isLoading: false,
      couponcode: {
        code: ''
      },
      status: {
        loadingItem: ''
      },
      carts: [],
      carttotal: '',
      cartfinaltotal: '',
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: '這是一段訊息'
      }
    }
  },
  created () {
    this.getProducts()
    this.getCart()
  },
  methods: {
    // 全部商品列表查詢
    getProducts (page = 1) {
      const vm = this
      // api路徑是不用經過驗證的路徑
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/products?page=${page}`
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        vm.isLoading = false
        vm.products = response.data.products
        vm.pagination = response.data.pagination
      })
    },
    // 取得單一商品細節
    getProduct (id) {
      $('#productModal').modal('show')
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/product/${id}`
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        vm.isLoading = false
        vm.product = response.data.product
      })
    },
    addtoCart (id, qty = 1) {
      const vm = this
      // 加入購物車
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/cart`
      vm.status.loadingItem = id
      const cart = {
        product_id: id,
        qty
      }
      this.$http.post(api, {data: cart}).then((response) => {
        console.log(response.data)
        vm.getCart()
        $('#productModal').modal('hide')
      })
    },
    getCart () {
      const vm = this
      // 取得購物車列表
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/cart`
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        console.log('取得購物車列表', response.data)
        vm.isLoading = false
        vm.carts = response.data.data.carts
        vm.carttotal = response.data.data.total
        vm.cartfinaltotal = response.data.data.final_total
      })
    },
    removeCartItem (id) {
      const vm = this
      // 刪除某一筆購物車資料
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/cart/${id}`
      this.$http.delete(api).then((response) => {
        console.log('刪除某一筆購物車資料', response.data)
        vm.getCart()
      })
    },
    appliedcoupon () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/coupon`
      this.$http.post(api, {data: vm.couponcode}).then((response) => {
        console.log('套用優惠券', response.data)
        vm.couponcode.code = ''
        vm.cartfinaltotal = response.data.data.final_total
        vm.getCart()
      })
    },
    createOrder () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/order`
      const order = vm.form
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(api, {data: order}).then((response) => {
            console.log('訂單建立', response.data)
            if (response.data.success) {
              vm.$router.push(`/customer_checkout/${response.data.orderId}`)
            }
            vm.getCart()
          })
        } else {
          console.log('欄位不完整')
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



