<template>
  <div class="products">
    <loading :active.sync="isLoading"></loading>
    <div class="d-flex justify-content-end">
      <button class="btn btn-primary" @click="openModal({}, true)" data-toggle="modal" data-target="#productModal">建立新的產品</button>
    <!-- <button class="btn btn-primary" @click="getProducts">商品查詢</button> -->
    </div>
    <!-- 商品的資料 -->
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120" class="text-center">分類</th>
          <th>產品名稱</th>
          <th width="120" class="text-center">原價</th>
          <th width="120" class="text-center">售價</th>
          <th width="100" class="text-center">是否啟用</th>
          <th width="120" class="text-center">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in products" :key="key">
          <td class="text-center">{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">{{item.origin_price | currency}}</td>
          <td class="text-right">{{item.price | currency}}</td>
          <td v-if="item.is_enabled ===  true" class="text-center">啟用</td>
          <td v-else class="text-center">未啟用</td>
          <td class="d-flex justify-content-around">
            <button class="btn btn-sm btn-outline-primary " @click="openModal(item, false)" data-toggle="modal" data-target="#productModal">編輯</button>
            <button class="btn btn-sm btn-outline-danger" @click="opendelModal(item, false)" data-toggle="modal" data-target="#delProductModal">刪除</button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- 產品分頁的切換 -->
    <nav aria-label="Page navigation example">
      <ul class="pagination justify-content-center">
        <li class="page-item" :class="{'disabled': pagination.has_pre === false}">
          <a class="page-link" href="#" @click.prevent="getProducts(pagination.current_page - 1)">Previous</a>
        </li>
        <li class="page-item" v-for="page in pagination.total_pages" :key="page"
          :class="{'active': pagination.current_page === page}">
          <a class="page-link" href="#" @click.prevent="getProducts(page)">{{page}}</a>
        </li>
        <li class="page-item" :class="{'disabled': pagination.has_next === false}">
          <a class="page-link" href="#" @click.prevent="getProducts(pagination.current_page + 1)">Next</a>
        </li>
      </ul>
    </nav>
    <!-- 新增商品的Modal  -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
    aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>新增產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image">輸入圖片網址</label>
                  <input type="text" class="form-control" id="image"
                    placeholder="請輸入圖片連結"
                    v-model="tempProduct.imageUrl">
                </div>
                <div class="form-group">
                  <label for="customFile">或 上傳圖片
                    <i class="fas fa-spinner fa-spin"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control"
                    ref="files" @change="uploadFile">
                </div>
                <img :src="tempProduct.imageUrl"
                  class="img-fluid" alt="">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title">標題</label>
                  <input type="text" class="form-control" id="title"
                    placeholder="請輸入標題"
                    v-model="tempProduct.title">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category">分類</label>
                    <input type="text" class="form-control" id="category"
                      placeholder="請輸入分類"
                      v-model="tempProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">單位</label>
                    <input type="unit" class="form-control" id="unit"
                      placeholder="請輸入單位"
                      v-model="tempProduct.unit">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                  <label for="origin_price">原價</label>
                    <input type="number" class="form-control" id="origin_price"
                      placeholder="請輸入原價"
                      v-model="tempProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">售價</label>
                    <input type="number" class="form-control" id="price"
                      placeholder="請輸入售價"
                      v-model="tempProduct.price">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description">產品描述</label>
                  <textarea type="text" class="form-control" id="description"
                    placeholder="請輸入產品描述"
                    v-model="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content">說明內容</label>
                  <textarea type="text" class="form-control" id="content"
                    placeholder="請輸入產品說明內容"
                    v-model="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox"
                      id="is_enabled"
                      v-model="tempProduct.is_enabled">
                    <label class="form-check-label" for="is_enabled">
                      是否啟用
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateProduct()">確認</button>
          </div>
        </div>
      </div>
    </div>
    <!-- 刪除商品的Modal -->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="deleteProduct()"
              >確認刪除</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: '',
  data () {
    return {
      products: [],
      pagination: {},
      tempProduct: {},
      isNew: false,
      isLoading: false
    }
  },
  created () {
    this.getProducts()
  },
  methods: {
    // 商品列表查詢
    getProducts (page = 1) {
      const vm = this
      // api路徑是不用經過驗證的路徑
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/products?page=${page}`
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        console.log(response.data)
        vm.isLoading = false
        vm.products = response.data.products
        vm.pagination = response.data.pagination
      })
    },
    // 課程教的寫法
    openModal (item, isNew) {
      if (isNew) {
        // 新的商品
        this.tempProduct = {}
        this.isNew = true
      } else {
        // 舊的商品
        // object.assign()的方法，是ES6的寫法，利用這種方法可以將item這個物件重新寫到一個新的物件裡面，並且可以避免前面的tempProduct跟item有傳物件參考的特性發生。
        this.tempProduct = Object.assign({}, item)
        this.isNew = false
      }
      $('#productModal').modal('show')
    },
    // 開啟delModal
    opendelModal (item, isNew) {
      if (isNew) {
        this.tempProduct = {}
        this.isNew = true
      } else {
        this.tempProduct = Object.assign({}, item)
        this.isNew = false
      }
      $('#delProductModal').modal('show')
    },
    // 商品資訊更新
    updateProduct () {
      // api路徑是需要經過驗證的路徑
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/product`
      let httpMethod = 'post'
      const vm = this
      // 判斷是否為新產品
      // 如果為舊商品，則使用put來做產品內容的修改
      if (vm.isNew === false) {
        // 舊商品
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'put'
        this.$http[httpMethod](api, {data: vm.tempProduct}).then((response) => {
          console.log(response.data)
          if (response.data.success) {
            $('#productModal').modal('hide')
            console.log('商品修改成功')
            vm.getProducts()
            // 清空tempProduct
            vm.tempProduct = {}
          } else {
            $('#productModal').modal('hide')
            console.log('修改失敗了')
            vm.getProducts()
          }
        })
      } else {
        // 新商品建立
        this.$http[httpMethod](api, {data: vm.tempProduct}).then((response) => {
          console.log(response.data)
          if (response.data.success) {
            $('#productModal').modal('hide')
            console.log('商品新增成功')
            vm.getProducts()
            vm.tempProduct = {}
          } else {
            $('#productModal').modal('hide')
            console.log('新增失敗了')
            vm.getProducts()
          }
        })
      }
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'put'
      }
    },
    // 商品刪除
    deleteProduct () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/product/${vm.tempProduct.id}`
      this.$http.delete(api).then((response) => {
        if (response.data.success) {
          console.log('商品刪除成功')
          vm.getProducts()
          $('#delProductModal').modal('hide')
          vm.tempProduct = {}
        } else {
          console.log('商品刪除失敗')
          vm.getProducts()
          $('#delProductModal').modal('hide')
        }
      })
    },
    uploadFile () {
      console.log(this)
      const uploadedFile = this.$refs.files.files[0]
      const vm = this
      // 利用formdata來模擬傳統表單送出的方式，可以使用append的方式將欄位新增進去。
      const formData = new FormData()
      formData.append('file-to-upload', uploadedFile)
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/upload`
      vm.isLoading = true
      this.$http.post(api, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((response) => {
        vm.isLoading = false
        console.log(response.data)
        if (response.data.success) {
          // vm.tempProduct.imageUrl = response.data.imageUrl
          // console.log(vm.tempProduct)
          // 使用set的方式將資料強制寫入
          // (寫入的地方，寫入的名稱/欄位，寫入的內容)
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl)
          console.log(vm.tempProduct)
        }
      })
      // this.$bus.$emit('message:push', response.data.message, 'danger')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



