<template>
  <div class="coupons">
    <div class="d-flex justify-content-end">
      <button class="btn btn-primary" @click="OpenCouponModal({}, true)">新增優惠券</button>
    </div>
    <table class="mx-auto mt-4 table table-striped">
      <thead>
        <tr class="">
        <th width="200" class="text-center">名稱</th>
        <th width="120" class="text-center">優惠碼</th>
        <th width="120" class="text-center">折扣百分比</th>
        <th width="120" class="text-center">到期日</th>
        <th width="120" class="text-center">是否啟用</th>
        <th width="120" class="text-center">編輯</th>
      </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in coupons" :key="key">
          <td>{{item.title}}</td>
          <td class="text-center">{{item.code}}</td>
          <td class="text-center">{{item.percent}}</td>
          <td class="text-center">{{item.due_date}}</td>
          <td v-if="item.is_enabled === true" class="text-center text-success">啟用</td>
          <td v-else class="text-center text-danger">未啟用</td>
          <td class="text-center">
            <button class="btn btn-sm btn-outline-primary" @click="OpenCouponModal(item, false)">編輯</button>
            <button class="btn btn-sm btn-outline-danger" @click="OpenCoupondelModal(item, false)">刪除</button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- newCoupon Modal -->
    <div class="modal fade" id="couponModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">新增優惠券</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="">優惠券名稱</label>
              <input type="text" v-model="tempcoupon.title" placeholder="請輸入優惠券名稱" class="form-control">
            </div>
            <div class="form-group">
              <label for="">折扣百分比</label>
              <input type="number" v-model="tempcoupon.percent" placeholder="請輸入百分比" max="100" min="1" class="form-control">
            </div>
            <div class="form-group">
              <label for="">優惠碼</label>
              <input type="text" v-model="tempcoupon.code" placeholder="請輸入優惠碼" class="form-control">
            </div>
            <div class="form-group">
              <label for="">到期日</label>
              <input type="text" v-model="tempcoupon.due_date" placeholder="請輸入到期日期，如: 20201220" class="form-control">
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  id="is_enabled"
                  v-model="tempcoupon.is_enabled">
                <label class="form-check-label" for="is_enabled">
                  是否啟用
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button class="btn btn-primary" @click="updateCoupon()">更新優惠券</button>
          </div>
        </div>
      </div>
    </div>
    <!-- coupon delModal  -->
    <div class="modal fade" id="coupondelModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog ">
        <div class="modal-content">
          <div class="modal-header bg-danger">
            <h5 class="modal-title" id="exampleModalLabel">移除優惠券</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <h5>確定要移除 <span class="text-danger">{{tempcoupon.title}}</span> 優惠券 ?</h5>
            </div>
          </div>
          <div class="modal-footer">
            <button class="btn btn-danger" @click="removeCoupon(tempcoupon.id)">確定移除</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'coupons',
  data () {
    return {
      isNew: false,
      tempcoupon: {},
      coupon: {
        title: '',
        is_enabled: false,
        percent: 0,
        due_date: '',
        code: ''
      },
      coupons: []
    }
  },
  created () {
    this.getCoupons()
  },
  methods: {
    OpenCouponModal (item, isNew) {
      const vm = this
      if (isNew === true) {
        // 新的優惠券
        vm.isNew = true
        vm.tempcoupon = {}
      } else {
        // 舊的優惠券
        vm.isNew = false
        vm.tempcoupon = Object.assign({}, item)
      }
      $('#couponModal').modal('show')
    },
    OpenCoupondelModal (item, isNew) {
      const vm = this
      if (isNew === true) {
        // 新的優惠券
        vm.isNew = true
        vm.tempcoupon = {}
      } else {
        // 舊的優惠券
        vm.isNew = false
        vm.tempcoupon = Object.assign({}, item)
      }
      $('#coupondelModal').modal('show')
    },
    addCoupon () {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/coupon`
      // const timestamp = Date()
      // console.log(timestamp)
      this.$http.post(api, {data: vm.coupon}).then((response) => {
        console.log(response.data)
        console.log('新增優惠券')
        $('#couponModal').modal('hide')
        vm.getCoupons()
      })
    },
    getCoupons (page = 1) {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/coupons?page=${page}`
      this.$http.get(api).then((response) => {
        console.log(response.data)
        console.log('取得優惠券列表')
        vm.coupons = response.data.coupons
      })
    },
    updateCoupon (id) {
      const vm = this
      if (vm.isNew) {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/coupon`
        this.$http.post(api, {data: vm.tempcoupon}).then((response) => {
          console.log(response.data)
          console.log('新增優惠券')
          vm.tempcoupon = {}
          $('#couponModal').modal('hide')
          vm.getCoupons()
        })
      } else {
        id = vm.tempcoupon.id
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/coupon/${id}`
        this.$http.put(api, {data: vm.tempcoupon}).then((response) => {
          console.log(response.data)
          console.log('修改優惠券')
          vm.tempcoupon = {}
          $('#couponModal').modal('hide')
          vm.getCoupons()
        })
      }
    },
    removeCoupon (id) {
      const vm = this
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/admin/coupon/${id}`
      this.$http.delete(api).then((response) => {
        console.log(response.data)
        console.log('移除優惠券')
        vm.tempcoupon = {}
        $('#coupondelModal').modal('hide')
        vm.getCoupons()
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



