    <AlertMessage></AlertMessage>
    <!-- 主要區域 -->
    <div class="container-fluid">
      <div class="row">
        <!-- sidebar -->
        <Sidebar :msg='currentPage'></Sidebar>
        <!-- main content -->
        <main role="main" class="col-md-9 ml-sm-auto col-lg-10 px-md-4 my-3">
          <!-- 放置內容的地方 -->
          <router-view></router-view>
        </main>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from '@/components/navbar'
import Sidebar from '@/components/sidebar'
import AlertMessage from '@/components/alertmessage'

export default {
  name: 'dashboard',
  data () {
    return {
      currentPage: ''
    }
  },
  components: {
    Navbar,
    Sidebar,
    AlertMessage
  }
  // methods: {
  //   Page () {
  //     this.currentPage = this.$route.path
  //   }
  // },
  // created () {
  //   this.Page()
  // }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body {
  font-size: .875rem;
}

.feather {
  width: 16px;
  height: 16px;
  vertical-align: text-bottom;
}

</style>



