<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  created () {
    const api = `${process.env.APIPATH}/api/${process.env.CUSTOMEAPIPATH}/products`
    this.$http.get(api).then((response) => {
      // console.log(response.data)
    })
    // console.log(process.env.APIPATH, process.env.CUSTOMEAPIPATH)
  }
}
</script>

<style lang="scss">
// 引進bootstrap的套件及啟用sass功能
// 為了方便自定義裡面的內容，所以改為使用底下的方式載入
@import "./assets/all";

html body{
  // 將字型改為微軟正黑體
  font-family: 'Microsoft JhengHei', 'Times New Roman', Times, serif;
}
</style>



